
import { defineComponent } from "vue";
import axios from "axios";
export default defineComponent({
  name: "AdminPanel",
  data() {
    return {
      showAddForm: false,
      wrongPassword: false,
      projectAdded: false,
      projectName: "",
      description: "",
      tech: "",
      git: "",
      live: "",
      image: "",
      created: "",
      renderList: false,
      passwordField: "",
      projectList: [],
    };
  },
  methods: {
    testClick(e: any) {
      e.preventDefault();
      const fd = this.addToFormData();
      this.sendRequest(fd);
    },
    addToFormData() {
      const fd = new FormData();
      fd.append("project_name", this.projectName);
      fd.append("description", this.description);
      fd.append("tech", this.tech);
      fd.append("git", this.git);
      fd.append("live", this.live);
      fd.append("project_image", this.image);

      return fd;
    },
    async sendRequest(formData: FormData) {
      try {
        axios
          .post("http://localhost:3333/add-project", formData)
          .then((response) => {
            if (response.data === 200) this.projectAddedShow();
          })
          .catch((e) => console.log(e));
      } catch (error) {
        console.log(error);
      }
    },
    imageHandler(ev: any) {
      this.image = ev.target.files[0];
    },
    projectAddedShow() {
      this.projectAdded = true;
    },
    logInUser() {
      const fd = new FormData();
      fd.append("password", this.passwordField);
      axios
        .post("http://localhost:3333/api/login", fd)
        .then((response) => {
          console.log(response.status);

          if (response.status === 200) {
            this.showAddForm = true;
            this.getProjectList();
          } else if (response.data === 401) this.wrongPassword = true;
        })
        .catch((e) => console.log(e));
    },
    async getProjectList() {
      try {
        const res = await axios.get("http://localhost:3333/api/projects");
        const data = await res.data;
        this.projectList = data;
        console.log(data);
        this.renderList = true;
      } catch (error) {
        console.log(error);
      }
    },
    async deleteProject(name: string) {
      try {
        const res = await axios
          .delete(`http://localhost:3333/delete-project/${name}`)
          .then((response) => {
            this.projectList = response.data;
          })
          .catch((er) => console.log(er));
      } catch (error) {
        console.log(error);
      }
    },
    async editProject() {
      const fd: FormData = this.addToFormData();
      fd.delete("project_image");
      fd.append("project_image", this.image);
      try {
        const res = await axios
          .post("http://localhost:3333/edit", fd)
          .then((response) => {
            this.projectList = response.data;
          })
          .catch((er) => console.log(er));
      } catch (error) {
        console.log(error);
      }
    },
    fillProjectData(proj: any) {
      console.log(proj);

      this.projectName = proj.name;
      this.description = proj.description;
      this.tech = proj.tech;
      this.git = proj.git;
      this.live = proj.live;
      this.image = proj.image;
    },
  },
});
